.product {
  &__photo {
    width: 100%;
    border-radius: 4px;
    transition: transform $effect-duration;

    @include hover(transform, scale(1.08));
    @include custom-cursor('/src/images/cursor-mouse-mice.png');
  }

  &__title {
    margin-top: 24px;
    margin-bottom: 8;
    font-family: 'Space Grotesk', sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 25.2px;
    text-align: left;
    color: $main-text-color;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    align-content: flex-end;
  }

  &__description {
    font-family: 'Space Mono', monospace;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;
    color: $main-text-color;
    margin: 0;

    &--desktop {
      height: 88px;
      margin-bottom: 12px;;
    }
  }

  &__price {
    font-family: 'Space Grotesk', sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    margin: 0;
    margin-top: 12px;
  }
}
